import React from 'react';
import { BrowserRouter as Router, Switch, Route, withRouter } from 'react-router-dom';
import Home from './Home';
import Contact from './Contact';
import Videoplayer from './Videoplayer';

//Rendered de componenten om door naar de URL te kijken.
const Switcher = () => (
    <Router>
        <Switch>
            <Route exact path="/buildE" component={withRouter(Home)} />
            <Route path="/buildE/Contact" component={withRouter(Contact)} />
            <Route path="/buildE/Videoplayer" component={withRouter(Videoplayer)} />
        </Switch>
    </Router>
)

export default Switcher;